import React, { Component } from 'react';
import Menu from './Menu';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from './Home';

class Main extends Component {
    render() {
        const TNA = "TNA";
        return (
            <BrowserRouter>
                <div>                    
                    <Menu />                                         
                    <Switch>
                        <Route exact path={`${TNA}/`} component={Home} />
                        <Route path={`${TNA}/home`} component={Home} />
                    </Switch>                                
                </div>
            </BrowserRouter>
        )
    }
}

export default Main;
